import React, { useState } from "react";
import { Form, Input, Button } from "antd";
import { useFormik } from "formik";
import * as Yup from "yup";
import { useDispatch } from "react-redux";
import { SIGN_UP_SAGA } from "../../redux/constants/Cyberbugs/Cyberbugs";
import { history } from "../../util/history";

export default function Register() {
  const [componentSize, setComponentSize] = useState("default");
  const dispatch = useDispatch();
  const onFormLayoutChange = ({ size }) => {
    setComponentSize(size);
  };

  const formik = useFormik({
    initialValues: {
      name: "",
      email: "",
      passWord: "",
      phoneNumber: ""
    },
    validationSchema: Yup.object({
      name: Yup.string()
        .min(6, "Ti khon phi trn 6 k t")
        .max(30, "Ti khon phi t hn 40 k t")
        .required("Khng c b trng!"),
      passWord: Yup.string()
        .min(6, "Password phi trn 6 k t")
        .required("Khng c b trng!"),

      email: Yup.string()
        .required("Khng c b trng!")
        .email("Email khng hp l! Vui lng nhp li"),
      //   hoTen: Yup.string().required("Khng c b trng!"),
      phoneNumber: Yup.string()
        .matches(
          /(03|05|07|08|09|01[2|6|8|9])+([0-9]{8})\b/,
          "S T khng hp l! Vui lng nhp li"
        )
        .required("Khng c b trng!")
    }),

    onSubmit: (values) => {
      console.log("valus", values);
      dispatch({
        type: SIGN_UP_SAGA,
        userInfo: values
      });
    }
  });
  return (
    <Form
      onSubmitCapture={formik.handleSubmit}
      labelCol={{
        span: 4
      }}
      wrapperCol={{
        span: 14
      }}
      layout="horizontal"
      initialValues={{
        size: componentSize
      }}
      onValuesChange={onFormLayoutChange}
      size={componentSize}
    >
      <h3 className="ml-3">Sign up</h3>
      <div style={{ height: 60 }}>
        <Form.Item label="Tn">
          <Input name="name" onChange={formik.handleChange} />
          {formik.errors.name ? formik.errors.name : ""}
        </Form.Item>
      </div>

      <div style={{ height: 60 }}>
        <Form.Item label="Password">
          <Input name="passWord" onChange={formik.handleChange} />
          {formik.errors.passWord ? formik.errors.passWord : ""}
        </Form.Item>
      </div>

      <div style={{ height: 60 }}>
        <Form.Item label="Email">
          <Input name="email" onChange={formik.handleChange} />
          {formik.errors.email ? formik.errors.email : ""}
        </Form.Item>
      </div>

      <div style={{ height: 60 }}>
        <Form.Item label="S in thoi">
          <Input name="phoneNumber" onChange={formik.handleChange} />
          {formik.errors.phoneNumber ? formik.errors.phoneNumber : ""}
        </Form.Item>
      </div>

      <Form.Item label="Tc v">
        <Button htmlType="submit" className="mr-2">
          ng k
        </Button>
        <Button
          onClickCapture={() => {
            history.push("login");
          }}
        >
          ng nhp
        </Button>
      </Form.Item>
    </Form>
  );
}
